import React, { useState } from 'react';
import CartAPIComponent from '../components/CartApiComponent';

const ItemCard = () => {
    const [selectedSize, setSelectedSize] = useState('');
    const cartAPI = CartAPIComponent();

    const handleSizeChange = (e) => {
        setSelectedSize(e.target.value);
    };

    const handleAddToBasket = () => {
        if (selectedSize) {
            const itemToAdd = {
                name: "Item",
                price: 19.99,
                size: selectedSize,
            };

            cartAPI.addItemToBasket(1, itemToAdd);
        } else {
            alert("Please select a size before adding to the basket.");
        }
    };

    return (
        <div className="item-details">
            <img className="item-image" src="../images/simu_liu2.jpg" alt="preview-image" />
            <div className="item-content">
                <h1 className="item-name">Item Name</h1>
                <p className="item-description">Item Description Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.</p>
                <p className="item-price">Price</p>
                <div className="item-size">
                    <label htmlFor="size">Size: </label>
                    <select id="size" value={selectedSize} onChange={handleSizeChange}>
                        <option value="">Select size</option>
                        <option value="S">S</option>
                        <option value="M">M</option>
                        <option value="L">L</option>
                        <option value="XL">XL</option>
                    </select>
                </div>
                <div className="add-to-basket">
                    <button onClick={handleAddToBasket}>Add to Basket</button>
                    <img src="../instagram.png" alt="Instagram Logo" />
                </div>
            </div>
        </div>
    );
};

export default ItemCard;
