import "../styles/Navbar.css";
import { getPosts } from './axios';
import { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import ListPage from "./ListPage";

const Navbar = () => {
  const [posts, setPosts] = useState([]);
  const [searchResults, setSearchResults] = useState([]);
  const [searchQuery, setSearchQuery] = useState('');

  useEffect(() => {
    getPosts().then(json => {
      setPosts(json);
      setSearchResults(json); // Initialize searchResults with all posts initially
    });
  }, []);

  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value);
    // Filter the posts based on the searchQuery
    const filteredResults = posts.filter(post =>
      post.title.toLowerCase().includes(event.target.value.toLowerCase())
    );
    setSearchResults(filteredResults);
  };

  return (
    <div className="topnav">
      <ul className="topnav-list">
        <li><Link to="/">New In</Link></li>
        <li><Link to="/premiere">Hot Now</Link></li>
        <li><Link to="/">Sale</Link></li>
        <li><Link to="/">Dresses</Link></li>
        <li><Link to="/swimwear">Swimsuits</Link></li>
        <li><Link to="/">Loungewear</Link></li>
        <li><Link to="/ken">Mens</Link></li>
      </ul>

      <div className="search-bar">
        <input
          type="text"
          placeholder="Search Store..."
          value={searchQuery}
          onChange={handleSearchChange}
        />
        <button type="button" onClick={() => setSearchQuery('')}>
          Clear
        </button>
      </div>

      {/* Pass the filtered searchResults to the ListPage component */}
      <ListPage searchResults={searchResults} />
    </div>
  );
};

export default Navbar;