import Footer from "./components/Footer";
import Header from "./components/Header";
import HomePage from "./pages/HomePage";
import ItemPage from "./pages/ItemPage";

import Navbar from "./components/Navbar";
import DressesPage from "./pages/DressesPage";
import NewInPage from "./pages/NewInPage";
import SwimsuitsPage from "./pages/SwimsuitsPage";
import TrousersPage from "./pages/TrousersPage";
import PremierePage from "./pages/PremierePage";
import KenPage from "./pages/KenPage";

import CartPage from "./pages/CartPage";
import WishlistPage from "./pages/WishlistPage";
import AccountPage from "./pages/AccountPage";

import { motion } from "framer-motion"
import { useState } from "react";
import { useEffect } from "react";
import { Routes, Route } from 'react-router-dom';
import "./App.css";

function App() {

  /* cursor */
  const[mousePosition, setMousePosition] = useState({
    x: 0,
    y: 0
  });
  console.log(mousePosition);

  useEffect(() => {
    const mouseMove = e => {
      setMousePosition({
        x: e.clientX,
        y: e.clientY
      })
    }

    window.addEventListener("mousemove", mouseMove);
    return () => {
      window.removeEventListener("mousemove", mouseMove);
    }
  }, []);

  const variants = {
    default: {
      x: mousePosition.x-20,
      y: mousePosition.y-18,
    }
  }

  return (
    <div>
      <motion.div
        className="cursor"
        variants={variants}
        animate="default"
        style={{
            position: "fixed",
            pointerEvents: "none",
            zIndex: 9999,
        }}
      />
      <Header/>
      <Navbar/>

      <Routes>
        <Route path='/' element={<HomePage />} />
        <Route path='/item-page/:itemId' element={<ItemPage />} />

        <Route path='/dresses' element={<DressesPage/>} />
        <Route path='/new-in' element={<NewInPage/>} />
        <Route path='/trousers' element={<TrousersPage/>} />
          
        <Route path='/premiere' element={<PremierePage />} />
        <Route path='/swimwear' element={<SwimsuitsPage/>} />
        <Route path='/ken' element={<KenPage />} />

        <Route path='/cart' element={<CartPage/>} />
        <Route path='/wishlist' element={<WishlistPage/>} />
        <Route path='/account' element={<AccountPage/>} />
      </Routes>

      <Footer/>
    </div>
  );
}

export default App;